@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  overflow-x: hidden;
  margin: 0;
}

.page-main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  min-width: 320px;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    margin: 0 auto;
    max-width: 1150px;
  }
}

.title {
  font-size: 25px;
  line-height: 30px;
  margin: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}

.link-button {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
  color: #444444;
  display: block;
  padding: 14px 38px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  border: 2px solid #444444;
}
.link-button:hover {
  color: #ffffff;
  background-color: #444444;
}
.link-button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .link-button {
    font-size: 17px;
  }
}

.top-menu {
  min-height: 80px;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .top-menu {
    background: linear-gradient(to bottom, #ffffff 88px, #d9d9d9 89px, transparent 90px);
    border-bottom: none;
  }
}

@media (max-width: 767px) {
  .top-menu__container {
    padding: 0;
  }
}

.main-nav {
  padding-top: 10px;
}
@media (min-width: 768px) {
  .main-nav {
    padding: 0;
  }
}

.main-nav__toggle {
  position: relative;
  display: none;
  width: 20px;
  height: 14px;
  padding-right: 45px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #000000;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    width: auto;
  }
}
@media (min-width: 1150px) {
  .main-nav__list {
    max-width: 500px;
  }
}

.main-nav__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.main-nav__logo {
  padding-top: 12px;
  order: 0;
}
@media (min-width: 768px) {
  .main-nav__logo {
    padding-top: 6px;
    margin-left: -1px;
    order: 1;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo {
    order: 2;
    margin-left: -63px;
  }
}

.main-nav__delivery {
  display: none;
  font-size: 15px;
  font-weight: 400;
  color: #444444;
  text-decoration: none;
}
.main-nav__delivery:hover {
  opacity: 0.6;
}
.main-nav__delivery:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__delivery {
    position: relative;
    display: block;
    padding: 18px 0 20px;
    order: 5;
    z-index: 1;
  }
}

@media (max-width: 767px) {
  .main-nav--close {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 32px 0;
  }
  .main-nav--close .main-nav__wrapper {
    position: absolute;
    z-index: 1;
    top: 10px;
    left: 0;
    background-color: #ffffff;
  }
  .main-nav--close .main-nav__toggle {
    display: block;
    margin-left: auto;
    z-index: 2;
  }
  .main-nav--close .main-nav__list {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--open {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 32px 0;
  }
  .main-nav--open .main-nav__wrapper {
    position: absolute;
    z-index: 1;
    top: 10px;
    left: 0;
    background-color: #ffffff;
  }
  .main-nav--open .main-nav__toggle {
    display: block;
    z-index: 2;
    margin-left: auto;
  }
  .main-nav--open .main-nav__toggle::after {
    top: 5px;
    width: 21px;
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--open .main-nav__toggle::before {
    top: 5px;
    width: 21px;
    transform: rotate(-45deg);
  }
  .main-nav--open .main-nav__list:not(:last-child) {
    display: block;
  }
}

.deals-list {
  display: none;
}
@media (min-width: 1150px) {
  .deals-list {
    position: relative;
    display: flex;
    order: 4;
    margin-left: -30px;
    z-index: 1;
  }
}

.deals-list__item {
  padding: 14px 31px 20px;
}

.deals-list__link {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  text-decoration: none;
}
.deals-list__link:hover {
  opacity: 0.6;
}
.deals-list__link:active {
  opacity: 0.3;
}

.site-list {
  margin-top: 16px;
  border-top: 1px solid #ececec;
}
@media (min-width: 768px) {
  .site-list {
    position: relative;
    order: 3;
    margin-top: 0;
    border-top: none;
    z-index: 1;
  }
}
@media (min-width: 1150px) {
  .site-list {
    order: 1;
    margin-left: -35px;
  }
}

@media (min-width: 768px) {
  .site-list__item:not(:last-child) {
    margin-right: 37px;
  }
}
@media (min-width: 1150px) {
  .site-list__item:not(:last-child) {
    margin-right: 2px;
  }
}

@media (min-width: 1150px) {
  .site-list__item:last-child {
    border-left: 1px solid #ececec;
  }
}

.site-list__link {
  display: block;
  padding: 29px 0 25px 76px;
  text-decoration: none;
  color: inherit;
  border-bottom: 1px solid #ececec;
}
.site-list__link:hover {
  opacity: 0.6;
}
.site-list__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .site-list__link {
    font-size: 15px;
    padding: 21px 0;
    border-bottom: none;
  }
}
@media (min-width: 1150px) {
  .site-list__link {
    font-size: 17px;
    padding: 34px 34px 33px;
  }
}

@media (min-width: 768px) {
  .user-list {
    width: 80%;
    justify-content: flex-end;
    order: 2;
  }
}
@media (min-width: 1150px) {
  .user-list {
    margin-right: 2px;
    order: 3;
    width: auto;
  }
}

@media (min-width: 768px) {
  .user-list__item {
    border-left: 1px solid #ececec;
  }
}

.user-list__link {
  display: block;
  text-decoration: none;
  color: inherit;
  padding: 28px 2px 26px 30px;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .user-list__link {
    padding: 32px 2px 33px 30px;
    border-bottom: none;
  }
}
.user-list__link:hover span {
  opacity: 0.6;
}
.user-list__link:active span {
  opacity: 0.3;
}

.user-list__icon {
  margin-right: 24px;
  vertical-align: sub;
  opacity: 0.2;
}
@media (min-width: 768px) {
  .user-list__icon {
    margin-right: 31px;
  }
}
.user-list__icon:hover {
  opacity: 1;
  fill: #63d1bb;
}

.user-list__link:hover .user-list__icon {
  fill: #63d1bb;
  opacity: 1;
}

.user-list__link:active .user-list__icon {
  fill: #63d1bb;
  opacity: 0.3;
}

@media (min-width: 768px) {
  .user-list__text--hide {
    display: none;
  }
}

@media (min-width: 768px) {
  .user-list__link--cart {
    padding: 31px 0 32px 30px;
  }
}
.user-list__link--cart .user-list__icon {
  margin-right: 21px;
}
@media (min-width: 768px) {
  .user-list__link--cart .user-list__icon {
    margin-right: 26px;
  }
}

.logo__link {
  display: block;
}

.logo__link[href] {
  cursor: pointer;
}
.logo__link[href]:hover {
  opacity: 0.6;
}
.logo__link[href]:active {
  opacity: 0.3;
}
.logo__link[href] picture {
  display: block;
}

@media (min-width: 768px) {
  .logo__link-img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .logo__link-img {
    width: 149px;
    height: 72px;
  }
}

.header {
  position: relative;
  background-color: #f9f9f9;
}
@media (min-width: 768px) {
  .header::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 24px;
    bottom: -7px;
    z-index: 1;
    background: url("../img/zigzag-fill.svg");
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 282px;
  background: url("../img/header-bg.jpg") no-repeat center;
  background-size: 320px auto;
  background-position-y: -18px;
}
@media (min-resolution: 2dppx) {
  .header__container {
    background: url("../img/header-bg@2x.jpg") no-repeat center;
    background-size: 320px auto;
    background-position-y: -18px;
  }
}
@media (min-width: 768px) {
  .header__container {
    margin-top: -64px;
    padding-top: 64px;
    min-height: 560px;
    align-items: stretch;
    background: url("../img/header-bg-tablet.jpg") no-repeat center;
    background-size: 768px auto;
    background-position-y: -67px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .header__container {
    background: url("../img/header-bg-tablet@2x.jpg") no-repeat center;
    background-size: 768px auto;
    background-position-y: -67px;
  }
}
@media (min-width: 1150px) {
  .header__container {
    min-height: 562px;
    background: url("../img/header-bg-desktop.jpg") no-repeat center;
    background-position-y: -65px;
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .header__container {
    background: url("../img/header-bg-desktop@2x.jpg") no-repeat center;
    background-size: 1150px auto;
    background-position-y: -65px;
  }
}

.header__heading {
  margin: 0;
  padding: 24px 0 73px;
  font-size: 27px;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    padding-top: 88px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .header__heading {
    padding: 94px 0;
    font-size: 53px;
    line-height: 57px;
  }
}

.header__heading--inner {
  padding: 76px 0;
  margin: 0;
  background-color: #f1f1f1;
}
@media (min-width: 768px) {
  .header__heading--inner {
    margin-top: -63px;
    padding: 98px 20px 72px;
  }
}
@media (min-width: 1150px) {
  .header__heading--inner {
    padding-bottom: 95px;
  }
}

.header__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .header__list {
    display: flex;
    justify-content: center;
    padding: 0 50px;
  }
}

@media (min-width: 768px) {
  .header__item {
    flex-grow: 1;
    min-width: 334px;
    max-width: 525px;
  }
}

.header__link {
  position: relative;
  display: block;
  padding: 24px 80px 31px 30px;
  font-size: 19px;
  color: #ffffff;
  text-decoration: none;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
@media (min-width: 768px) {
  .header__link {
    font-size: 20px;
    line-height: 30px;
    padding-top: 26px;
    padding-left: 36px;
    padding-bottom: 37px;
  }
  .header__link:hover {
    transform: translateY(-5px);
  }
  .header__link:hover .header__icon {
    opacity: 0.7;
  }
  .header__link:active .header__icon {
    opacity: 0.3;
  }
}

.header__link--interior {
  background-color: #63d1bb;
}
.header__link--interior:hover {
  background-color: #59d5bc;
}

.header__link--toy {
  background-color: #56c3b2;
}
.header__link--toy:hover {
  background-color: #46c1ae;
}

.header__list-container {
  padding: 0;
}

.header__icon {
  position: absolute;
  top: 23px;
  right: 36px;
  opacity: 0.5;
  fill: #149080;
}
@media (min-width: 768px) {
  .header__icon {
    top: 30px;
    right: 36px;
  }
}

.header__icon--toy {
  top: 21px;
  right: 27px;
}
@media (min-width: 768px) {
  .header__icon--toy {
    top: 31px;
    right: 34px;
  }
}

.featured {
  position: relative;
  padding-top: 31px;
  padding-bottom: 42px;
}
.featured::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 6px;
  bottom: 0;
  background: url("../img/zigzag.svg");
}
@media (min-width: 768px) {
  .featured {
    padding-top: 48px;
    padding-bottom: 70px;
  }
  .featured::after {
    width: 100%;
    height: 17px;
    background-size: cover;
  }
}
@media (min-width: 1150px) {
  .featured {
    padding-top: 128px;
    padding-bottom: 100px;
  }
  .featured::after {
    width: 1050px;
    height: 23px;
    left: calc(50% - 525px);
    background-size: cover;
  }
}

.featured__top {
  position: relative;
}
@media (min-width: 768px) {
  .featured__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 32px;
  }
}
@media (min-width: 1150px) {
  .featured__top {
    padding: 0 175px;
    margin-bottom: 31px;
  }
}

@media (min-width: 768px) {
  .featured__bottom {
    display: grid;
    grid-template-columns: minmax(292px, 1fr) 1fr;
    grid-template-rows: min-content 1fr min-content minmax(min-content, 52px);
    column-gap: 42px;
  }
}
@media (min-width: 1150px) {
  .featured__bottom {
    grid-template-rows: min-content 1fr min-content minmax(min-content, 52px);
    column-gap: 58px;
    padding: 0 0 0 175px;
  }
}

.featured__heading {
  margin-bottom: 5px;
}

.featured__badge {
  margin: 0;
  margin-bottom: 16px;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .featured__badge {
    max-width: 130px;
    margin-top: 6px;
    margin-right: -2px;
    line-height: 17px;
    text-align: center;
  }
}
@media (min-width: 1150px) {
  .featured__badge {
    position: absolute;
    top: 2px;
    left: 16px;
  }
}

.featured__text {
  margin: 0;
  margin-bottom: 18px;
  font-size: 15px;
  font-weight: 400;
  color: #444444;
}
@media (min-width: 768px) {
  .featured__text {
    margin-bottom: 37px;
    font-size: 17px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .featured__text {
    padding-right: 10px;
    margin-bottom: 48px;
  }
}

.featured__image-container {
  margin: 0;
}
@media (min-width: 768px) {
  .featured__image-container {
    grid-column: 2/3;
    grid-row: 1/5;
    margin-bottom: -6px;
    padding-top: 12px;
  }
}

.featured__img {
  margin-bottom: 21px;
}
@media (min-width: 768px) {
  .featured__img {
    width: 334px;
    height: 404px;
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .featured__img {
    margin-top: -166px;
    width: 525px;
    height: 636px;
  }
}

.featured__price {
  margin: 0;
  margin-bottom: 23px;
  color: #444444;
  text-align: center;
}
@media (min-width: 768px) {
  .featured__price {
    margin-bottom: 0;
    padding-top: 60px;
    padding-bottom: 32px;
    font-size: 20px;
    line-height: 24px;
  }
}
@media (min-width: 1150px) {
  .featured__price {
    padding-top: 0;
  }
}

.specs {
  border-collapse: collapse;
  margin-bottom: 30px;
  width: 100%;
}
@media (min-width: 768px) {
  .specs {
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .specs {
    margin-bottom: 113px;
  }
}

.specs__heading {
  padding: 0;
  padding-right: 3px;
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
  color: #444444;
  text-align: left;
}
@media (min-width: 768px) {
  .specs__heading {
    padding-right: 0;
    width: 97px;
  }
}

.specs__description {
  padding: 0;
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
  color: #444444;
  text-align: left;
}

.specs__row {
  height: 40px;
  border-bottom: 1px solid #ececec;
}

.advantages {
  padding-bottom: 28px;
}
@media (min-width: 768px) {
  .advantages {
    padding-bottom: 54px;
  }
}
@media (min-width: 1150px) {
  .advantages {
    padding: 0 175px 60px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 235px 1fr;
    grid-template-rows: repeat(3, 1fr);
    row-gap: 27px;
    column-gap: 100px;
    grid-auto-flow: column;
  }
}
@media (min-width: 1150px) {
  .advantages__list {
    column-gap: 115px;
  }
}

.advantages__heading {
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .advantages__heading {
    margin-bottom: 43px;
  }
}

.advantages__item {
  position: relative;
  margin-bottom: 28px;
  line-height: 20px;
  padding-top: 55px;
}
.advantages__item:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .advantages__item {
    font-size: 20px;
    line-height: 22px;
  }
}

.advantages__item--flower-icon::before {
  content: "";
  position: absolute;
  width: 33px;
  height: 38px;
  top: 0;
  left: 8px;
  background: url("../img/flower.svg");
}

.advantages__item--money-icon::before {
  content: "";
  position: absolute;
  width: 47px;
  height: 27px;
  top: 1px;
  left: 0;
  background: url("../img/money.svg");
}

.advantages__item--heart-icon::before {
  content: "";
  position: absolute;
  width: 34px;
  height: 27px;
  top: 6px;
  left: 7px;
  background: url("../img/heart.svg");
}

.advantages__item--ball-icon::before {
  content: "";
  position: absolute;
  width: 35px;
  height: 34px;
